import React from 'react';
import { useState, useEffect } from 'react';
import { NotionRenderer } from 'react-notion';

import { Link } from "react-router-dom";
import { PageContent, PageLink, PageWrapper } from "constants/Mixins";
import styled from "styled-components";

// For styling markdown content
import 'react-notion/src/styles.css';
import './writing.css';

function RiskAversion() {
  const [data, setData] = useState({});

  useEffect(() => {
    fetch(
      'https://notion-api.splitbee.io/v1/page/2d7a76e151164833a52abb2d6c484204'
    )
      .then((res) => res.json())
      .then((data) => setData(data));
  }, []);

  return (
    <PageWrapper className='writing'>
        <div className="writing">
            <Link to="/writing">
                <PageLink>&#8592; Writing</PageLink>
            </Link>
            <h1>risk aversion</h1>
            <h4>thoughts preceding big life decisions shouldn't be as draining as you think</h4>
            <p>2022-02-11</p>
            <NotionRenderer blockMap={data} />
        </div>
    </PageWrapper>
    
  );
}

export default RiskAversion;
